import pubfooter from '@/components/pubfooter/pubfooter'
import axios from 'axios'
import tools from '@/common/tools.js'

export default {
  name: 'personal',
  data: function() {
    return {
      userData: {},
      adData: []
    }
  },
  created() {
    this.$store.commit('LOADING_SHOW', true)
    this._getData()
  },
  activated() {
    this._getData()
  },
  methods: {
    _getData() {
      const url = '/Mobile/User/index.html'
      axios.get(url)
        .then((res) => {
          console.log(res)
          this.$store.commit('LOADING_SHOW', false)
          if (res.data.user.status == 1) {
            this.userData = res.data.user.result
            this.adData = res.data.ad
            console.log('用户信息：', this.userData)
            this.$store.commit('SET_USERINFO', this.userData)
            // tools.setStorage('userInfo', this.userData)
          } else {
            tools.showError(res.user.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    gotoAd(id) {
      this.$router.push({path: 'equipdetails', query: { id: id}})
    }
  },
  components: {
    pubfooter
  }
}



